<template>
    <div>
        <app-header></app-header>
        <div class="left-side-block">
            <div class="accordion" id="accordion">
                <div class="card">
                    <div class="card-header" id="substation">
                        <h5 class="mb-0">
                            <button class="btn btn-link" type="button" data-toggle="collapse" data-target="#collapseSubstation" aria-expanded="false" aria-controls="collapseSubstation">
                                Подстанции
                            </button>
                        </h5>
                    </div>
                    <div id="collapseSubstation" class="collapse" aria-labelledby="substation" data-parent="#accordion">
                        <div class="card-body">
                            <ul>
                                <li>Подстанция 2035</li>
                                <li>Подстанция 2036</li>
                                <li>Подстанция 2037</li>
                                <li>Подстанция 2038</li>
                                <li>Подстанция 2039</li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div id="map" class="map">
            <!--<iframe src="http://yandex.ru/map-widget/v1/?um=constructor%3Aa02ac7f52d0f38c49523506839cd4a9f51315128569b2d6a05b6e432d84c28f7&amp;source=constructor" width="617" height="457" frameborder="0"></iframe>-->
        </div>
        <div class="right-side-block"></div>
    </div>
</template>
<script>
import AppHeader from './main/Header.vue';
export default {
    data() {
        return {
            
        }
    },
    components: {
        AppHeader
    },
    methods: {

    },
    mounted() {
        var myMap;
        ymaps.ready(init); // Ожидание загрузки API с сервера Яндекса
        function init () {
            myMap = new ymaps.Map("map", {
            apiKey: '9b29614f-8f0b-4b79-a9e9-c837c4101f71',
            center: [44.03, 43.07], // Координаты центра карты
            zoom: 10 // Zoom
            });
        }
    }
}
</script>

<style scoped>
.left-side-block, .right-side-block {
    display: inline-block;
    position: absolute;
    width: 25%;
    height: 100%;
}
.map {
    position: absolute;
    left: 25%;
    width: 50%;
    height: 100%;
    padding-top: 6px;
}
.left-side-block {
    float: left;
}
.right-side-block {
    right: 0;
}
</style>


